import React from 'react'
import autoBind from "react-autobind"
import { Link } from "react-router-dom"
import Navbar from "./Navbar"
import firebase from "./Firestone"


export default class UpdateCourse extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            action: this.props.match.params.action,
            id: this.props.match.params.id,
            course: this.props.match.params.id_course,
            module: {
                nome: '',
                promemoria: '',
                tempo: '',
                minimo: '',
                tipo: 'Risorsa',
                risorsa: '',
                nome_risorsa: '',
                abilitato: true,
                course: this.props.match.params.id_course,
            },
            file: null
        }
        if(this.props.match.params.id!=='0'){
            this.getModuleData(this.props.match.params.id)
        }
        autoBind(this)
    }
    async getModuleData(id){
        console.log('get')
        const db = firebase.firestore();
        let module = null
        await db.collection("moduli").doc(id).get().then((querySnapshot) => {
            module = {...querySnapshot.data()}
        });
        this.setState({module: {...this.state.module, ...module}})
        console.log(this.state.module)
    }

    prepareData(){
        dataToSend = this.state
        if(this.state.module.tipo === 'Risorsa' && this.state.file){
            fileUpload(this.state.file)
        }
        else{
            sendData()
        }
    }

    render(){
        return(
            <>
                <Navbar/>
                <div className="container">
                    <div className="row">
                        <div className="col-11">
                            <div className="h1 my-4 float-left">
                                Modulo
                            </div>
                        </div>
                        <div className="col-1">
                            <Link to={"/modules/"+this.state.course} className="float-right my-4 p-4">
                                <img src={require('../App/img/icon/close.png')} className="w-100" alt="" />
                            </Link>
                        </div>
                    </div>
                    <div className="row mx-0 mt-4">
                        <hr className="col-12"/>
                            <div className="col-6">
                                <p className="fontS14 mb-2">
                                    Nome
                                </p>
                                <input type="text"
                                    value={this.state.module.nome}
                                    onChange={(e)=>this.setState({module:{...this.state.module,nome: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-6">
                                <p className="fontS14 mb-2">
                                    Promemoria
                                </p>
                                <input type="text"
                                    value={this.state.module.promemoria}
                                    onChange={(e)=>this.setState({module:{...this.state.module,promemoria: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-4">
                                <p className="fontS14 mb-2">
                                    Tempo (In minuti)
                                </p>
                                <input type="text"
                                    value={this.state.module.tempo}
                                    onChange={(e)=>this.setState({module:{...this.state.module,tempo: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-4">
                                <p className="fontS14 mb-2">
                                    Punteggio minimo
                                </p>
                                <input type="text"
                                    value={this.state.module.minimo}
                                    onChange={(e)=>this.setState({module:{...this.state.module,minimo: e.target.value}})}
                                    className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                />
                            </div>
                            <div className="col-4">
                                <p className="fontS14 mb-2">
                                    Abilitato
                                </p>
                                <select
                                    value={this.state.module.abilitato}
                                    onChange={(e)=>this.setState({module:{...this.state.module,abilitato: e.target.value}})}
                                    className="h48 rounded fontS14 textBlueDeep"
                                    >
                                    <option value={true}>Abilitata</option>
                                    <option value={false}>Disabilitata</option>
                                </select>
                            </div>
                            <div className="col-4">
                                <p className="fontS14 mb-2">
                                    Tipo
                                </p>
                                <select
                                    value={this.state.module.tipo}
                                    onChange={(e)=>this.setState({module:{...this.state.module,tipo: e.target.value}})}
                                    className="h48 rounded fontS14 textBlueDeep">
                                    <option value="Risorsa">Risorsa</option>
                                    <option value="Quiz">Quiz</option>
                                </select>
                            </div>
                            {this.state.module.tipo === 'Risorsa'
                                ?<div className="col-8 row mx-0 pl-0">
                                    <div className="col-6 pr-2">
                                    <p className="fontS14 mb-2">
                                        Nome File
                                    </p>
                                    <input type="text"
                                        value={this.state.module.nome_risorsa}
                                        onChange={(e)=>this.setState({module:{...this.state.module,nome_risorsa: e.target.value}})}
                                        className="h48 fontS14 textBlueDeep px-3 w-100 rounded"
                                    />
                                </div>
                                    <div className="col-6 pr-0 pl-4">
                                        <p className="fontS14 mb-2">
                                            File
                                        </p>
                                        <input type="file" 
                                            files={[this.state.file]}
                                            onChange={(e)=>this.setState({file:e.target.files[0]})}
                                            className="h48 fontS14 textBlueDeep w-100 rounded pt-2"
                                        />
                                    </div>
                                </div>
                                :null
                            }
                            {this.state.type === 'Quiz'
                                ?<div className="col-12">
                                    <p className="fontS14 mb-2">
                                        Domande
                                    </p>
                                </div>
                                :null
                            }
                            <div className="col-9"></div>
                            <div className="col-3">
                                <button className="btn btn-success h48 w-100 mt-3" onClick={this.prepareData}>
                                    Salva
                                </button>
                            </div>
                    </div>
                </div>
            </>
        )
    }
}

var dataToSend

function sendData(){
    const db = firebase.firestore();
    if(dataToSend.id!=='0'){
        db.collection("moduli").doc(dataToSend.id).set({
            ...dataToSend.module
        })
        .then(function(docRef) {
            window.history.back()
        })
        .catch(function(error) {
            alert(error)
        })
    }
    else{
        db.collection("moduli").add({
            ...dataToSend.module
        })
        .then(function(docRef) {
            window.history.back()
        })
        .catch(function(error) {
            alert(error)
        })
    }
}

function fileUpload(file){
    var storageRef = firebase.storage().ref();
    dataToSend.nome_risorsa = dataToSend.nome_risorsa?dataToSend.nome_risorsa:file.name
    var percorso = storageRef.child(`risorse/${dataToSend.nome_risorsa}`);
    percorso.put(file).then(function(snapshot) {
        snapshot.ref.getDownloadURL().then((url) => {
            dataToSend.module.risorsa = url
            sendData()
        })
    });
}
